angular
    .module( 'app', [
        'ui.router',
        'ngCookies'
    ]);

angular
    .module( 'app' );

angular.module( 'app' )
       .config( [ '$urlRouterProvider', '$stateProvider', ( $urlRouterProvider, $stateProvider ) => {

           $urlRouterProvider.otherwise( '/' );

           $stateProvider
               .state( 'home', {

                   url: '/',
                   templateUrl: 'app/templates/homepage.html',
                   controller: 'HomePageController'

               })
               .state( 'dashboard', {

                   url: '/dashboard',
                   templateUrl: 'app/templates/dashboard.html',
                   controller: 'DashboardController'

               })
               .state( 'newConference', {

                   url: '/conferences/new',
                   templateUrl: 'app/templates/new-conference.html',
                   controller: 'NewConferenceController'

               })
               .state( 'editConference', {

                   url: '/conferences/:guid',
                   templateUrl: 'app/templates/edit-conference.html',
                   controller: 'EditConferenceController'

               })
               .state( 'registrationForm', {

                   url: '/conferences/:guid/registration',
                   templateUrl: 'app/templates/forms/registration.html',
                   controller: 'RegistrationFormController'

               })
               .state( 'delegateInformationForm', {

                   url: '/conferences/:guid/delegates',
                   templateUrl: 'app/templates/forms/delegate-information.html',
                   controller: 'DelegateInformationFormController'

               });

       }]);

angular.module( 'app' )
       .controller( 'BasePageController', [ '$cookies', '$state', '$window', 'RestApiService', function( $cookies, $state, $window, $rest ) {

            const data = $cookies.get( 'loginData' );
            this.showLogout = $cookies.get( 'isLoggedIn' ) && data;
            this.Logout = () => {

                const promise = $rest.Logout( JSON.parse( data ) );
                promise.then( ( data ) => {

                    $cookies.remove( 'isLoggedIn' );
                    $cookies.remove( 'loginData' );

                } ).catch( ( err ) => {

                    $cookies.remove( 'isLoggedIn' );
                    $cookies.remove( 'loginData' );

                } );

                this.showLogout = false;
                $window.location = "/";

            };


       } ] );

angular.module( 'app' )
       .controller( 'DashboardController', [ 'RestApiService', '$cookies', '$state', function( $rest, $cookies, $state ) {

           let isLoggedIn = $cookies.get( 'isLoggedIn' )
               , data       = ( $cookies.get( 'loginData' ) );

           if( !isLoggedIn && !data ) {
               return $state.go( 'home' );
           }

           data = JSON.parse( data );

           let dataPromise = $rest.GetUser( data );
           dataPromise.then( ( dataOut ) => {

               this.data = dataOut;


           } ).catch( ( data ) => {

               console.log( 'Error Encountered' );

           } );

           dataPromise = $rest.GetAllConferences( data );
           dataPromise.then( ( dataIn ) => {

               this.hasConferences = ( dataIn.length !== 0 );
               this.conferences = {
                   count: dataIn.length,
                   data: dataIn
               };

           } ).catch( ( data ) => {

               console.log( 'Error Encountered' );

           } );


       } ] );

angular.module( 'app' )
       .controller( 'EditConferenceController', [ '$stateParams', '$cookies', '$http', '$state', '$window', 'RestApiService', function( $stateParams, $cookies, $http, $state, $window, $rest ) {

           const isLoggedIn = $cookies.get( 'isLoggedIn' );
           let data = ( $cookies.get( 'loginData' ) );

           data = JSON.parse( data );

           const conferenceGuid = $stateParams.guid;
           this.guid = conferenceGuid;

           $rest.GetConference( {

               conferenceGuid,
               token: data.token,
               guid: data.guid,

           } ).then( ( data ) => {

               this.conferenceData = data;

           } );

       } ] );

angular.module( 'app' )
       .controller( 'HomePageController', [ 'RestApiService', '$state', '$cookies', '$window', function( $restApi, $state, $cookies, $window ) {

           const isLoggedIn = $cookies.get( 'isLoggedIn' )
               , data       = ( $cookies.get( 'loginData' ) );

           if( isLoggedIn && data ) {
               $state.go( 'dashboard' );
           }

           const self = this;
           self.Login = () => {

               const resultPromise = $restApi.Login( self.user );
               resultPromise.then( ( dataLogin ) => {

                   $cookies.put( 'loginData', JSON.stringify( dataLogin ) );
                   $cookies.put( 'isLoggedIn', true );

                   $window.location.reload();
                   $state.go( 'dashboard' );

               } ).catch( ( dataError ) => {

                   if( dataError.loggedIn === false ) {

                       self.hasMessage = true;
                       self.messageText = dataError.message;
                       self.messageClass = {
                           red: true
                       }

                   }

               } );

           }

       } ] );

angular.module( 'app' )
       .controller( 'NewConferenceController', [ '$cookies', '$http', '$state', '$window', 'RestApiService', function( $cookies, $http, $state, $window, $rest ) {

           const isLoggedIn = $cookies.get( 'isLoggedIn' );
           let data = ( $cookies.get( 'loginData' ) );

           const self = this;

           data = JSON.parse( data );

           this.countries = [

               {

                   code: 'IN',
                   name: 'India'

               },

               {

                   code: 'US',
                   name: 'America'

               }

           ];

           this.populateStateList = ( country ) => {

               $rest.GetStates( country ).then( ( data ) => {

                   this.states = data;

               } );

           };

           this.cancel = () => {

               $state.go( 'dashboard' );

           };

           this.NewConference = () => {

               this.processing = true;

               const dataObject = {

                   token: data.token,
                   guid: data.guid,
                   registration: {

                       name: self.conference.registration.facultyAdvisor.firstName + ' ' + self.conference.registration.facultyAdvisor.lastName,
                       address: {

                           street: self.conference.registration.address.street,
                           city: self.conference.registration.address.city,
                           pin: self.conference.registration.address.pin,
                           state: self.conference.registration.address.state,
                           country: self.conference.registration.address.country,

                       },

                       facultyAdvisor: {

                           name: self.conference.registration.facultyAdvisor.firstName + ' ' + self.conference.registration.facultyAdvisor.lastName,
                           mobileNumber: self.conference.registration.facultyAdvisor.mobile,
                           landlineNumber: self.conference.registration.facultyAdvisor.landline,
                           email: self.conference.registration.facultyAdvisor.email

                       }

                   }

               };

               $rest.CreateConference( dataObject ).then( ( data ) => {

                    if( data.data.statusCode === 200 ) {

                        this.processing = false;
                        $state.go( 'dashboard' );

                    } else {

                        this.isError = true;
                        this.errorMessage = data.data.data.message;

                    }

               } ).catch( ( data ) => {

                  this.isError = true;
                  this.errorMessage = data.message;

               } );

           };

       } ] );

angular
    .module( 'app' )
    .factory( 'CryptoService', [ function() {

        return {

            getSha512: function( data ) {

                var shaObj = new jsSHA( "SHA-512", "TEXT" );
                shaObj.update( data );
                return shaObj.getHash( "HEX" );

            }

        };

    } ] );

angular
    .module( 'app' )
    .factory( 'RestApiService', [ '$http', 'CryptoService', '$q', ( $http, $cryptoService, $q ) => {

        const apiUrl = 'http://api.base.dsmun.com:3345'

        return {

            Login( userObject ) {

                const dataObject = {}
                    , promise    = $q.defer();

                dataObject.username = userObject.username;
                dataObject.password = $cryptoService.getSha512( userObject.password );

                $http.post( apiUrl + '/auth', dataObject ).then( ( data ) => {

                    if( data.status === 200 && data.data.data.token ) {

                        promise.resolve( {

                            token: data.data.data.token,
                            guid : data.data.data.guid,
                            role : data.data.data.role

                        } );

                    } else {

                        promise.reject( {

                            message: data.data.message,
                            loggedIn: false

                        } );

                    }

                } ).catch( ( data ) => {

                    promise.reject( {

                        message: data.data.message,
                        loggedIn: false

                    } );

                } );

                return promise.promise;

            },

            GetUser( data ) {

                const promise    = $q.defer();

                $http.get( `${ apiUrl }/users/${ data.guid }?token=${ data.token }` ).then( ( data ) => {

                    if( data.status === 200 && data.data.data ) {

                        promise.resolve( data.data.data[ 0 ] );

                    } else {

                        promise.reject( {

                            message: data.data.message,
                            error: true

                        } );

                    }

                } ).catch( ( data ) => {

                    promise.reject( {

                        message: data.data.message,
                        error: true

                    } );

                } );

                return promise.promise;

            },

            GetAllConferences( data ) {

                const promise    = $q.defer();

                $http.get( `${ apiUrl }/conferences?guid=${ data.guid }&token=${ data.token }` ).then( ( data ) => {

                    if( data.status === 200 && data.data.data ) {

                        promise.resolve( data.data.data );

                    } else {

                        promise.reject( {

                            message: data.data.message,
                            error: true

                        } );

                    }

                } ).catch( ( data ) => {

                    promise.reject( {

                        message: data.data.message,
                        error: true

                    } );

                } );

                return promise.promise;

            },

            GetConference( dataIn ) {

                const promise    = $q.defer();

                $http.get( `${ apiUrl }/conferences/${ dataIn.conferenceGuid }?token=${ dataIn.token }&guid=${ dataIn.guid }` ).then( ( data ) => {

                    if( data.status === 200 && data.data.data ) {

                        promise.resolve( data.data.data[ 0 ] );

                    } else {

                        promise.reject( {

                            message: data.data.message,
                            error: true

                        } );

                    }

                } ).catch( ( data ) => {

                    promise.reject( {

                        message: data.data.message,
                        error: true

                    } );

                } );

                return promise.promise;

            },

            Logout( dataIn ) {

                const promise = $q.defer();

                $http.post( apiUrl + '/deauth', {

                    guid: dataIn.guid,
                    token: dataIn.token,

                } ).then( ( data ) => {

                    if( data.status === 200 ) {

                        promise.resolve( {

                            statusCode: 200

                        } );

                    } else {

                        promise.reject( {

                            message: data.data.message,
                            error: true

                        } );

                    }

                } ).catch( ( data ) => {

                    promise.reject( {

                        message: data.data.message,
                        error: true

                    } );

                } );

                return promise.promise;

            },

            GetStates( country ) {

                const promise    = $q.defer();

                $http.get( `${ apiUrl }/metadata/states/${ country }` ).then( ( data ) => {

                    if( data.status === 200 && data.data.data ) {

                        promise.resolve( data.data.data );

                    } else {

                        promise.reject( {

                            message: data.data.message,
                            error: true

                        } );

                    }

                } ).catch( ( data ) => {

                    promise.reject( {

                        message: data.data.message,
                        error: true

                    } );

                } );

                return promise.promise;

            },

            CreateConference( confUserObject ) {

                const promise = $q.defer();

                const dataObject = {

                    token: confUserObject.token,
                    guid: confUserObject.guid,
                    data: {

                        registration: confUserObject.registration,

                    }

                };

                $http.post( apiUrl + '/conferences', dataObject ).then( ( data ) => {

                    if( data.status === 200 ) {

                        promise.resolve( {

                            data: data.data,

                        } );

                    } else {

                        promise.reject( {

                            message: data.data.message,
                            error: true

                        } );

                    }

                } ).catch( ( data ) => {

                    promise.reject( {

                        message: data.data.message,
                        error: true

                    } );

                } );

                return promise.promise;

            },

            UpdateConference( confUserObject ) {

                const promise = $q.defer();

                const dataObject = {

                    token: confUserObject.token,
                    guid: confUserObject.guid,
                    data: confUserObject.data

                };

                $http.put( apiUrl + '/conferences/' + confUserObject.conferenceGuid, dataObject ).then( ( data ) => {

                    if( data.status === 200 ) {

                        promise.resolve( {

                            data: data.data,

                        } );

                    } else {

                        promise.reject( {

                            message: data.data.message,
                            error: true

                        } );

                    }

                } ).catch( ( data ) => {

                    promise.reject( {

                        message: data.data.message,
                        error: true

                    } );

                } );

                return promise.promise;

            },

        };

    } ] );

